
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
.border-right{border-right:solid 1px #00111f
}
.border-left{border-left:solid 1px #00111f
}
.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
#activity,#review{padding-top:48px
}
@media screen and (max-height:380px){
#activity-titlebar{display:none
}
#activity,#review{padding-top:0
}
}
body{padding-top:55px
}
#activity{position:absolute;top:0;right:0;bottom:0;left:0
}
#activity.blur{-webkit-filter:blur(5px);filter:blur(5px)
}
#review{position:absolute;top:0;right:0;bottom:0;left:0;display:none
}
#review.blur{-webkit-filter:blur(5px);filter:blur(5px)
}
#help{display:table;position:absolute;top:-100%;right:0;bottom:0;left:0;width:100%;height:100%;z-index:1000
}
#help.not-visible{animation:HIDE .5s;animation-fill-mode:both
}
#help.visible{top:0;background-color:rgba(0,0,0,0.3);animation:SHOW .5s;animation-fill-mode:both
}
@keyframes SHOW{
from{opacity:0
}
to{opacity:1
}
}
@keyframes HIDE{
from{opacity:1;top:0
}
to{opacity:0;top:-100%
}
}
#help p{margin-bottom:3px
}
#help hr{border-color:#483d8b;margin-top:10px;margin-bottom:10px
}
#help .help-container{height:100%;display:table-cell;vertical-align:middle;position:absolute;bottom:0;top:0;left:0;right:0;padding:15px;padding-top:25px
}
#help .help-container .help-box{max-width:420px;max-height:100%;overflow-y:scroll;margin:auto;padding:10px;border-radius:5px;background-color:#eaeaea;color:#483d8b;box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
#help .help-container .help-box>*{width:100%
}
#help .help-container .help-box .help-secondary{color:#666669
}
#help .help-container .help-box .help-title{font-family:serif;border-bottom:solid 1px #483d8b;padding-bottom:5px;margin-bottom:10px
}
#help .help-container .help-box .help-title .glyphicon{float:right;cursor:pointer;padding:1px
}
#help .help-container .help-box .help-content{width:100%
}
#help .help-container .help-box .btn-help{background-color:#483d8b;border-color:#483d8b;color:#eaeaea;margin-top:10px
}


