
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
.flex-column{display:flex;flex-direction:column
}
.flex-zero{flex:0
}
.flex-one{flex:1
}
.flex-two{flex:2
}
.flex-three{flex:3
}
.flex-row{display:flex;flex-direction:row
}
.flex-center{justify-content:center
}
.flex-bottom{justify-content:flex-end
}
.actions-content{padding-bottom:30px
}
.action-header{display:table
}
.action-header .delete-action{color:#FF6961;display:table-cell;vertical-align:middle;padding:0 10px;margin-bottom:0;border-right:solid 1px #494959;font-size:20px;cursor:pointer
}
.action-header .action-title{display:table-cell;vertical-align:middle;width:100%;font-size:18px;font-family:'Sinkin';padding:8px;margin-bottom:0
}
.action-header .step-value{color:#77DD77;display:table-cell;vertical-align:middle;padding:0 8px;border-left:solid 1px #494959;font-size:18px;min-width:30px;text-align:center
}
.action-instruction{font-size:19px
}
.action-instruction .flow{color:#FFB347;top:4px
}
.instruction-label{color:#D4AF37;border-right:solid 1px #D4AF37;padding-right:10px;margin-right:10px
}
.action-instruction-sublabel{text-align:center;color:#bbb;font-size:14px;margin:0
}
.step-actions{padding-bottom:8px
}
.step-actions p{margin-bottom:0
}
.step-actions .clear-step{color:#aA4B49;text-decoration:underline;white-space:nowrap
}
.action-components{border-bottom:solid 1px #494959;border-top:solid 1px #494959;padding-top:8px;padding-bottom:8px;margin-bottom:8px;width:100%;overflow:hidden
}
.action-components .btn{flex:1;margin-left:2px;margin-right:2px
}
.action-components .btn:focus,.action-components .btn:hover{background-color:transparent;box-shadow:none
}
.action-components .btn.completed{border-bottom-color:#77DD77 !important;border-bottom-width:2px
}
.action-components .btn.current{background-color:#292934;border-color:#00CFF0
}
.action-components .cancel-button{border-left:solid 1px #292934;padding-top:2px;padding-right:4px;padding-left:8px;margin-left:8px
}
.action-components .cancel-button img{width:30px;height:30px
}
.action-add-button{margin-top:8px
}
.finished-button{margin-top:8px
}
.action-items{color:#bbb
}
.step-selection{padding:10px
}
.step-selection p{font-size:18px
}
#tense-selector{padding-bottom:11px
}
#tense-selector button{flex:1;letter-spacing:2px;margin:0 5px
}


