
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
.border-right{border-right:solid 1px #00111f
}
.border-left{border-left:solid 1px #00111f
}
.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.btn{font-size:16px;letter-spacing:1px
}
.btn:focus{outline:none
}
.btn-raised{background-color:#292934;border-color:#292934;box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.btn-raised:hover,.btn-raised:focus{color:white;background-color:#494959;border-color:#494959;outline:none;box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.btn-raised.alt{background-color:transparent;color:#292934;font-weight:bolder;letter-spacing:1px
}
.btn-raised.alt:hover{color:#f2f2f2
}
.btn-raised2{background-color:#494959;border-color:#494959;box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.btn-raised2:hover,.btn-raised2:focus{color:white;background-color:#494959;border-color:#494959;outline:none;box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.btn-raised2.alt{background-color:transparent;color:#f2f2f2;font-weight:bolder;letter-spacing:1px
}
.btn-raised2.alt:hover{color:#f2f2f2
}
.btn-primary{background-color:#9A3B39;border-color:#9A3B39;box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.btn-primary:hover{background-color:#aA4B49;border-color:#9A3B39;outline:none
}
.btn-primary.alt{background-color:transparent;color:#aA4B49;font-weight:bolder;letter-spacing:1px
}
.btn-primary.alt:hover{color:#f2f2f2
}
.btn-primary.disabled,.btn-primary.disabled:hover{background-color:#fff;color:#111;border-color:transparent
}
.btn-actionable{background-color:#D4AF37;border-color:#D4AF37;text-shadow:1px 0 1px rgba(50,50,50,0.8);letter-spacing:1px
}
.btn-actionable.alt,.btn-xs.btn-actionable.alt{background-color:transparent;color:#D4AF37;border-color:#D4AF37
}
.btn-actionable.disabled,.btn-actionable.disabled:hover{background-color:#EAE0D8;color:#111;border-color:transparent;text-shadow:none
}
.btn-actionable:hover{color:white;background-color:#C49F27;box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.list-group{border-radius:0
}
.list-group a{color:white
}
.list-group a:hover{color:white;background-color:#424245
}
.list-group .list-group-item{color:white;background-color:transparent;box-shadow:0 1px 2px rgba(0,0,0,0.5);border:none;border-radius:0;margin-bottom:5px
}
.form-control:focus{border-color:#D4AF37;box-shadow:inset 0 1px 1px rgba(0,0,0,0.075),0 0 9px #D4AF37;color:white;font-size:16px
}
.col-xs-5c,.col-sm-5c,.col-md-5c,.col-lg-5c{position:relative;min-height:1px;padding-right:5px;padding-left:5px
}
.col-xs-5c{width:20%;float:left
}
@media (min-width:768px){
.col-xs-5c,.col-sm-5c,.col-md-5c,.col-lg-5c{padding-right:10px;padding-left:10px
}
}
@font-face{font-family:'Alegreya';src:url('../../static/fonts/AlegreyaSans-Regular.ttf');src:url('../../static/fonts/AlegreyaSans-Regular.ttf') format('truetype'),
}
@font-face{font-family:'SinkinThin';src:url('/static/fonts/sinkin-sans/SinkinSans-100Thin.otf')
}
@font-face{font-family:'Sinkin';src:url('/static/fonts/sinkin-sans/SinkinSans-200XLight.otf')
}
@font-face{font-family:'Homizio';src:url('/static/fonts/HomizioRegular.otf')
}
@font-face{font-family:'HomizioLight';src:url('/static/fonts/HomizioNova.otf')
}
.brand-font{font-family:'Sinkin'
}
body{color:white;font-family:'Source Sans Pro',sans-serif;font-size:16px;background-color:#001318;padding-top:10px;padding-bottom:55px;height:100%
}
#app{height:100%
}
h2{color:#eee
}
a{color:white
}
a:hover{color:#9A3B39;text-decoration:none
}
@media (max-width:400px){
p{font-size:15px
}
}
.light{font-weight:lighter
}
::-webkit-scrollbar{width:2px;height:2px
}
::-webkit-scrollbar-track{background:transparent
}
::-webkit-scrollbar-thumb{background:#292934
}
hr{border-color:#292934
}
::-webkit-input-placeholder{color:#797989;font-style:italic
}
::-moz-placeholder{color:#797989;font-style:italic
}
:-ms-input-placeholder{color:#797989;font-style:italic
}
:-moz-placeholder{color:#797989;font-style:italic
}
.nopad{padding:0
}
.nopad-top{padding-top:0
}
.nopad-bottom{padding-bottom:0
}
.nopad-left{padding-left:0
}
.nopad-right{padding-right:0
}
.main-background{background-color:#001318;padding-top:8px
}
.bottompad{padding-bottom:200px
}
.bottombar{padding:5px;padding-bottom:8px;box-shadow:0 -3px 12px rgba(0,0,0,0.9);z-index:100;background:#001318;background:linear-gradient(#292934 0, #001318 20px, #001318);border-top:solid 1px #494959
}
.bottombar.nopad{padding:0
}
.actionbar{position:fixed;bottom:0;z-index:100;width:100%;padding:5px;box-shadow:0 -1px 2px rgba(0,0,0,0.9);background-color:#001318
}
.actionbar button{letter-spacing:1px
}
.flex-v{display:flex;flex-direction:column;height:100%
}
.flex-1{flex:1;overflow-y:scroll
}
.actionbar-flex{flex:none;z-index:100;width:100%;padding:5px;box-shadow:0 -3px 15px rgba(0,0,0,0.9);background-color:#292934
}
.actionbar-flex button{letter-spacing:1px
}
.vfull{height:100%
}
.vthird{height:33.33%
}
.hthird{width:33.33%
}
.scrolly{overflow-y:scroll
}
.scrollx{overflow-x:scroll
}
.add-button,.edit-button{width:100%;height:100%;cursor:pointer
}
.add-button circle,.edit-button circle,.add-button line,.edit-button line{stroke:#D4AF37
}
.close-button{width:100%;height:100%;cursor:pointer
}
.close-button circle,.close-button line{stroke:#aA4B49
}
#alert{position:fixed;z-index:3000;display:none;height:100%;width:100%;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0.4)
}
#alert #ok-alert-button,#alert .alert-confirm{display:none
}
#alert.ok-alert #ok-alert-button{display:inherit
}
#alert.confirm-alert .alert-confirm{display:inherit
}
#alert .alert-container{display:table-cell;vertical-align:middle;padding:10px
}
#alert .alert-container .alert-box{background-color:#eaeaea;color:#483d8b;max-width:400px;margin:auto;padding:10px;padding-left:20px;padding-right:20px;border-radius:4px;box-shadow:0 1px 15px rgba(0,0,0,0.9);font-size:18px;letter-spacing:.5px
}
#alert .alert-container .alert-box #alert-message{font-size:18px;text-align:center
}
#alert .alert-container .alert-box p{margin-bottom:0
}
#alert .alert-container .alert-box .btn-alert{background-color:#483d8b;border-color:#483d8b;color:#eaeaea;margin-top:20px
}
#alert .alert-container .alert-box .btn-alert.alt{background-color:transparent;color:#483d8b
}
#alert .alert-container .alert-box .btn-alert:hover{background-color:#00CFF0;color:#483d8b
}
#alert .alert-container .alert-box .alert-confirm .col-xs-6{padding:8px
}


