
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
#authorize-div{display:table;background-color:#494959;padding:10px;padding-right:20px;box-shadow:0 1px 2px rgba(0,0,0,0.5);cursor:pointer
}
@media screen and (max-width:767px){
#authorize-div{padding-left:10px
}
}
@media (min-width:768px){
#authorize-div{padding-left:20px
}
}
#authorize-div:hover{box-shadow:0 0 15px rgba(250,240,230,0.5)
}
.drive-text{display:table-cell;vertical-align:middle;width:100%
}
.drive-logo{display:table-cell;float:left
}
@media screen and (max-width:767px){
.drive-logo{width:50px;height:50px;margin-right:15px
}
}
@media (min-width:768px){
.drive-logo{width:80px;height:80px;margin-right:25px
}
}
.drive-connect-label{margin-bottom:0
}
@media screen and (max-width:767px){
.drive-connect-label{font-size:14px
}
}
@media (min-width:768px){
.drive-connect-label{font-size:16px
}
}
.drive-connect{color:#D4AF37;margin-bottom:0
}
@media screen and (max-width:767px){
.drive-connect{font-size:23px
}
}
@media (min-width:768px){
.drive-connect{font-size:35px
}
}


