
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
.text-input-titlebar{padding-left:10px;padding-right:10px;padding-top:5px;padding-bottom:5px;border-bottom:solid 1px #292934;display:table
}
.text-input-titlebar .text-input-title{display:table-cell;width:100%;vertical-align:middle;letter-spacing:2px;font-family:serif
}
.text-input-titlebar .text-input-done{display:table-cell
}
.text-input{padding:10px;border-bottom:solid 2px #494959
}
.text-input:focus{outline:none
}


