
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
.card{background-color:#292934;box-shadow:0 1px 2px rgba(0,0,0,0.5);margin-bottom:20px
}
.card .card-content{padding:20px
}
@media (max-width:400px){
.card .card-content{padding:12px
}
}
.card.nopad .card-content{padding:0
}
.card-header{border-bottom:solid 1px #00111f;background-color:#9A3B39
}
.card-header .card-title{color:#f2f2f2;border-right:solid 1px #00111f;padding:10px;font-size:18px;letter-spacing:2px;margin-right:15px
}
.card-header .card-subtitle{color:#f2f2f2;font-size:18px;padding:10px
}


