
.accent{color:#ffeacc
}
.actionable{color:#D4AF37
}
.muted{color:#bbb
}
.back-green{background-color:#77DD77 !important
}
.back-orange{background-color:#FFB347 !important
}
.back-red{background-color:#FF6961 !important
}
.back-purple{background-color:#B19CD9 !important
}
.orange{color:#FFB347
}
.blue{color:#00CFF0
}
.purple{color:#B19CD9
}
.red{color:#FF6961
}
.green{color:#77DD77
}
.shadow{box-shadow:0 1px 2px rgba(0,0,0,0.5)
}
.shadow-light{box-shadow:0 1px 2px rgba(200,200,200,0.5)
}
.shadow-long{box-shadow:0 1px 15px rgba(0,0,0,0.9)
}
html{height:100%
}
body{background-image:url('/static/bible-midnight-fade.png');background-position:top;background-repeat:no-repeat;background-size:cover;height:100%
}
#home-content{padding-bottom:50px
}
.testament-button{padding:23px 5px 20px 5px;border-bottom:solid 2px transparent;float:left;width:50%;letter-spacing:1px
}
.testament-button:hover{color:white;text-decoration:none;border-bottom:solid 2px #8d1
}
.testament-button:first-child{border-right:solid 1px #001318
}
.testament-button:last-child{border-left:solid 1px #001318
}
.loading-view{color:#00CFF0
}
.user-img{height:20px;width:20px;margin-right:8px;border-radius:5px
}
.recent-studies-label{color:#bbb
}
.recent-studies-label .studies-count{margin-left:5px;padding-left:5px;border-left:solid 2px #494959
}
.study{padding-left:10px;padding-right:10px;margin-bottom:10px
}
.study:last-child{margin-bottom:0
}
.study .bible{display:inline-block;width:54px;margin-left:24px;border-left:solid 1px #292934
}
.study-label{background-color:#494959;box-shadow:0 1px 2px rgba(0,0,0,0.5);border:solid 1px transparent;border-left:solid 3px transparent;padding:10px;padding-left:5px
}
.study-label:hover{border-left:solid 3px #8d1;cursor:pointer
}
.study-label p{margin:0
}
.refresh-studies{color:#00CFF0;cursor:pointer
}
.home-bottom{padding:10px
}
.home-bottom .user{cursor:pointer
}
.sign-in-view{text-align:center
}
.sign-in-view a{min-width:300px;max-width:400px;margin-top:10px
}
.sign-in-view p{margin-bottom:3px;text-align:center;color:#bbb
}


